html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

.usajobs-assistive-text {
  position: absolute !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  clip: rect(0 0 0 0) !important;
}

.usajobs-joa {
  font-size: 1.3rem;
}

.usajobs-joa .usajobs-nav,
.usajobs-joa .usajobs-site-header,
.usajobs-joa .usajobs-nav-secondary,
.usajobs-joa .usajobs-site-header,
.usajobs-joa .usajobs-joa-header,
.usajobs-joa .usajobs-joa-prev-next,
.usajobs-joa .usajobs-joa-actions__col-apply,
.usajobs-joa .usajobs-joa-actions__col-sub-actions,
.usajobs-joa .usa-footer,
.usajobs-joa .usajobs-joa__authenticity-note {
  display: none;
}

.usajobs-joa .usajobs-joa-banner__title {
  font-family: "Source Sans Pro", "Helvetica Neue", "Helvetica", "Roboto", "Arial", sans-serif;
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 0.5rem;
  margin-top: 1.5rem;
  font-size: 2.4rem;
  margin: 0;
}

.usajobs-joa .usajobs-joa-banner--pilot__dept,
.usajobs-joa .usajobs-joa-banner--pilot__agency {
  color: #000000;
}

.usajobs-joa .usa-accordion-button {
  background-image: inherit;
  background-color: #ffffff;
  padding-left: 0;
}

.usajobs-joa .usa-accordion-bordered .usa-accordion-content,
.usajobs-joa .usa-accordion-content[aria-hidden="true"] {
  display: block !important;
  border: 0px;
  box-shadow: inherit;
  padding-left: 0;
  padding-right: 0;
}

.usajobs-joa .usajobs-joa-summary--pilot__list > .usajobs-joa-summary--pilot__item,
.usajobs-joa .usajobs-joa-sub-section__container {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 48.82117%;
}

.usajobs-joa .usajobs-joa-summary--pilot__list > .usajobs-joa-summary--pilot__item:last-child,
.usajobs-joa .usajobs-joa-sub-section__container:last-child {
  margin-right: 0;
}

.usajobs-joa .usajobs-joa-summary--pilot__list
> .usajobs-joa-summary--pilot__item:nth-child(2n),
.usajobs-joa .usajobs-joa-sub-section__container:nth-child(2n) {
  margin-right: 0;
}

.usajobs-joa .usajobs-joa-actions--pilot.is-wide {
  margin: 0;
  padding: 0;
}

.usajobs-joa .usajobs-joa-section--pilot__header {
  font-family: "Source Sans Pro", "Helvetica Neue", "Helvetica", "Roboto", "Arial", sans-serif;
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 0.5rem;
  margin-top: 1.5rem;
  font-size: 2.4rem;
}

.usajobs-joa .usajobs-joa-pilot__shell {
  padding-top: 0.5rem;
}

.usajobs-joa .usajobs-content-well,
.usajobs-joa .usajobs-rightrail {
  margin: 0;
}

.usajobs-joa .usajobs-content-well .img-responsive,
.usajobs-joa .usajobs-rightrail .img-responsive {
  display: none;
}

.usajobs-joa .usajobs-content-well .row::after,
.usajobs-joa .usajobs-rightrail .row::after {
  clear: both;
  content: "";
  display: block;
}

.usajobs-joa .usajobs-content-well h3,
.usajobs-joa .usajobs-rightrail h3 {
  font-size: 1.6rem;
  margin: 1rem 0 0.5rem;
}

.usajobs-joa .usajobs-content-well h3 + p,
.usajobs-joa .usajobs-rightrail h3 + p {
  margin-top: 0;
}

.usajobs-joa .usajobs-content-well h4,
.usajobs-joa .usajobs-rightrail h4 {
  font-family: "Source Sans Pro", "Helvetica Neue", "Helvetica", "Roboto", "Arial", sans-serif;
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 0.5rem;
  margin-top: 1.5rem;
  font-size: 1.6rem;
  margin: 0;
}

.usajobs-joa .usajobs-content-well ul,
.usajobs-joa .usajobs-rightrail ul {
  margin-bottom: 0.25rem;
}

.usajobs-joa .usajobs-content-well ul li,
.usajobs-joa .usajobs-rightrail ul li {
  line-height: 1.2;
}

.usajobs-joa .usajobs-content-well p,
.usajobs-joa .usajobs-rightrail p {
  font-size: 1.3rem;
  line-height: 1.2;
}

.usajobs-joa .usajobs-content-well br,
.usajobs-joa .usajobs-rightrail br {
  display: block;
  margin: 0;
  line-height: 1;
}

.usajobs-joa .usajobs-content-well p + br + p,
.usajobs-joa .usajobs-rightrail p + br + p {
  margin-top: 0;
}

.usajobs-joa .usajobs-content-well a:after,
.usajobs-joa .usajobs-rightrail a:after {
  display: block;
  content: " (" attr(href) ") ";
  font-size: 1.1rem;
  font-weight: normal;
}

.usajobs-joa .usajobs-content-well .usajobs-joa-intro-hiring-paths--pilot__list
> .usajobs-joa-intro-hiring-paths--pilot__item,
.usajobs-joa .usajobs-rightrail .usajobs-joa-intro-hiring-paths--pilot__list
> .usajobs-joa-intro-hiring-paths--pilot__item {
  float: none;
  width: 100%;
}
